<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="banben">
           <span><img src="../assets/images/logo.png"></span>
           <span>超维</span>
        </div>
        <p class="comname">环链科技</p>
        <p class="comeng">Tach Chain Technology (Hangzhou) Co., Ltd.</p>
        <div class="softapp">
          
          <p class="comhttp">http://www.tachchain.org</p>
        </div>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar,
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('mycenter.developers'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      content:''
    }
  },
  created(){
    this.about()
  },
  methods:{
    // 软件开发商
    about(){
      var that = this
      this.axios({
        url:this.Url + 'SystemContent.ashx',
        method:'post',
        params: {req:5,id:1}
      }).then(res=>{
        if (res.data.success) {
          this.content = res.data.data
        }
      }).catch(err => {
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yingyong{
  padding-top: .9rem
}
.softapp{
  padding: 0.5rem;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%
}
.comname{
  font-size: .4rem!important;
  color: #333;
  text-shadow: 5px 2px 1px #ccc;
  letter-spacing: 5px;
  margin-bottom: .2rem;
  text-align: center;
}
.comeng,.comhttp{
  text-shadow: 2px 2px 1px #ccc;
  font-size: .3rem;
  color: #666;
  text-align: center;
}
.yd-cell-item{
  border-bottom:1px solid #f4f4f4;
}
.yd-cell-item span:nth-child(2){
  font-size: .3rem;
  color: #333;
}
.banben{
  padding: .8rem .5rem;
  text-align: center;
}
.banben span:nth-child(1){
  display: block;
  width: 2rem;
  height: 2rem;
  /*background: #ff6767;*/
  margin: 0 auto .3rem;
}
.banben span:nth-child(1) img{
  width: 100%
}
.banben span:nth-child(2){
  font-size: .26rem;
  color: #666;
}
.banben p{
  text-align: justify;
  line-height: 1.5;
  padding-top: .5rem;
  font-size: .28rem;
}
</style>



